<template>
  <div class="table">
    <div class="container">
      <div class="handle-box">
        <el-button
          type="primary"
          size="mini"
          class="handle-del mr10"
          @click="delAll"
          >Batch Delete</el-button
        >
        <el-input
          v-model="select_word"
          size="mini"
          placeholder="Select keywords"
          class="handle-input mr10"
        ></el-input>
        <el-button
          type="primary"
          size="mini"
          @click="centerDialogVisible = true"
          >Add song list</el-button
        >
      </div>
      <el-table
        :data="data"
        border
        size="mini"
        style="width: 100%"
        height="550px"
        ref="multipleTable"
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="40"></el-table-column>
        <el-table-column label="Image" width="100" align="center">
          <template slot-scope="scope">
            <img :src="getUrl(scope.row.pic)" alt="" style="width: 80px;" />
            <el-upload
              class="upload-demo"
              :action="uploadUrl(scope.row.id)"
              :show-file-list="false"
              :on-success="handleAvatarSuccess"
              :before-upload="beforeAvatarUpload"
            >
              <el-button size="mini">Update image</el-button>
            </el-upload>
          </template>
        </el-table-column>
        <el-table-column
          prop="title"
          label="Title"
          width="200"
          align="center"
        ></el-table-column>
        <el-table-column label="Introduction">
          <template slot-scope="scope">
            <p style="height: 100px; overflow: scroll">
              {{ scope.row.introduction }}
            </p>
          </template>
        </el-table-column>
        <el-table-column label="Style" width="100" align="center">
          <template slot-scope="scope">
            <div>{{ scope.row.style }}</div>
          </template>
        </el-table-column>
        <el-table-column label="Content" width="80" align="center">
          <template slot-scope="scope">
            <el-button size="mini" @click="getContent(data[scope.$index].id)"
              >Content</el-button
            >
          </template>
        </el-table-column>
        <el-table-column label="Comment" width="80" align="center">
          <template slot-scope="scope">
            <el-button size="mini" @click="getComment(data[scope.$index].id)"
              >Comment</el-button
            >
          </template>
        </el-table-column>
        <el-table-column label="Operation" width="150" align="center">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.row)"
              >Modify
            </el-button>
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.row.id)"
              >Delete
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="pagination">
        <el-pagination
          @current-change="handleCurrentChange"
          background
          layout="total, prev, pager, next"
          :current-page="currentPage"
          :page-size="pageSize"
          :total="tableData.length"
        >
        </el-pagination>
      </div>
    </div>

    <el-dialog
      title="Add song list"
      :visible.sync="centerDialogVisible"
      width="400px"
      center
    >
      <el-form
        :model="registerForm"
        status-icon
        ref="registerForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="Song list " prop="title" size="mini">
          <el-input
            v-model="registerForm.title"
            placeholder="Song list "
          ></el-input>
        </el-form-item>
        <el-form-item
          label="Song list introduction"
          prop="introduction"
          size="mini"
        >
          <el-input
            v-model="registerForm.introduction"
            placeholder="Song list introduction"
          ></el-input>
        </el-form-item>
        <el-form-item label="Style" prop="style" size="mini">
          <el-input v-model="registerForm.style" placeholder="Style"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="mini" @click="centerDialogVisible = false"
          >Cancel</el-button
        >
        <el-button type="primary" size="mini" @click="addsongList"
          >OK</el-button
        >
      </span>
    </el-dialog>

    <el-dialog title="Modify" :visible.sync="editVisible" width="600px">
      <el-form ref="form" :model="form" label-width="100px">
        <el-form-item label="Title" size="mini">
          <el-input v-model="form.title"></el-input>
        </el-form-item>
        <el-form-item label="Introduction" size="mini">
          <el-input type="textarea" v-model="form.introduction"></el-input>
        </el-form-item>
        <el-form-item label="Style" size="mini">
          <el-input v-model="form.style"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="mini" @click="editVisible = false">Cancel</el-button>
        <el-button type="primary" size="mini" @click="saveEdit">OK</el-button>
      </span>
    </el-dialog>

    <el-dialog title="Hint" :visible.sync="delVisible" width="300px" center>
      <div class="del-dialog-cnt" align="center">
        Deletion cannot be recovered. Are you sure to delete it?
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button size="mini" @click="delVisible = false">Cancel</el-button>
        <el-button type="primary" size="mini" @click="deleteRow">OK</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mixin } from "../mixins";
import { HttpManager } from "../api/index";

export default {
  name: "song-list-page",
  mixins: [mixin],
  data() {
    return {
      registerForm: {
        title: "",
        introduction: "",
        style: ""
      },
      tableData: [],
      tempDate: [],
      multipleSelection: [],
      centerDialogVisible: false,
      editVisible: false,
      delVisible: false,
      select_word: "",
      form: {
        id: "",
        title: "",
        pic: "",
        introduction: "",
        style: ""
      },
      pageSize: 5,
      currentPage: 1,
      idx: -1
    };
  },
  computed: {
    data() {
      return this.tableData.slice(
        (this.currentPage - 1) * this.pageSize,
        this.currentPage * this.pageSize
      );
    }
  },
  watch: {
    select_word: function() {
      if (this.select_word === "") {
        this.tableData = this.tempDate;
      } else {
        this.tableData = [];
        for (let item of this.tempDate) {
          if (item.title.includes(this.select_word)) {
            this.tableData.push(item);
          }
        }
      }
    }
  },
  created() {
    this.getData();
  },
  methods: {
    uploadUrl(id) {
      return `${this.$store.state.HOST}/songList/img/update?id=${id}`;
    },

    getData() {
      this.tableData = [];
      this.tempDate = [];
      HttpManager.getSongList().then(res => {
        this.tableData = res;
        this.tempDate = res;
        this.currentPage = 1;
      });
    },

    handleCurrentChange(val) {
      this.currentPage = val;
    },
    getContent(id) {
      this.$router.push({ path: "/listSong", query: { id: id } });
    },
    getComment(id) {
      this.$router.push({ path: "/Comment", query: { id: id, type: 1 } });
    },

    handleEdit(row) {
      this.idx = row.id;
      this.form = {
        id: row.id,
        title: row.title,
        pic: row.pic,
        introduction: row.introduction,
        style: row.style
      };
      this.editVisible = true;
    },

    saveEdit() {
      let params = new URLSearchParams();
      params.append("id", this.form.id);
      params.append("title", this.form.title);
      params.append("pic", this.form.pic);
      params.append("introduction", this.form.introduction);
      params.append("style", this.form.style);
      HttpManager.updateSongListMsg(params)
        .then(res => {
          if (res.code === 1) {
            this.notify("Modified successfully", "success");
            this.getData();
          } else {
            this.notify("Failed to modify", "error");
          }
        })
        .catch(err => {
          console.log(err);
        });
      this.editVisible = false;
    },
    // Add song list
    addsongList() {
      let params = new URLSearchParams();
      params.append("title", this.registerForm.title);
      params.append("pic", "/img/songListPic/123.jpg");
      params.append("introduction", this.registerForm.introduction);
      params.append("style", this.registerForm.style);
      HttpManager.setSongList(params)
        .then(res => {
          if (res.code === 1) {
            this.getData();
            this.registerForm = {};
            this.notify("Successfully added", "success");
          } else {
            this.notify("Failed to add ", "error");
          }
        })
        .catch(err => {
          console.log(err);
        });
      this.centerDialogVisible = false;
    },

    deleteRow() {
      HttpManager.deleteSongList(this.idx)
        .then(res => {
          if (res) {
            this.getData();
            this.notify("Deleted successfully", "success");
          } else {
            this.notify("Failed to delete", "error");
          }
        })
        .catch(err => {
          console.log(err);
        });
      this.delVisible = false;
    }
  }
};
</script>

<style scoped>
.handle-box {
  margin-bottom: 20px;
}

.handle-input {
  width: 300px;
  display: inline-block;
}
.pagination {
  display: flex;
  justify-content: center;
}
</style>



